<style lang="less" rel="stylesheet/less" scoped>
.input-hidden {
    width: 0;
    height: 0;
    overflow: hidden;
}
.modal-title {
    font-size: 1rem;
}
.dropdown-menu {
    left: -100px;
}
.dropdown-item {
    width: 240px;
    padding-right: 0.5rem;
    .fa {
        margin-top: 5px;
        color: chocolate !important;
    }
}
.fa-plus {
    color: #5f5fa8;
}
.remove-warpper {
    width: 30px;
    text-align: center;
    cursor: pointer;
    border-radius: 5px;
    &:hover {
        background-color: darken(rgb(248, 245, 240), 10%);
    }
}
</style>
<template>
  <div>
    <nav class="navbar navbar-expand-lg navbar-light bg-light">
      <router-link class="navbar-brand"
                   to="/#/dashboard">ISHOW</router-link>
      <!-- <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button> -->

      <div class="collapse navbar-collapse"
           id="navbarSupportedContent">
        <ul class="navbar-nav ml-auto"
            v-if="$root.userinfo.username">
          <li class="nav-item create active"
              style="margin-right: 100px;"
              @click="createShow">
            <a class="nav-link"
               href="javascript:void(0);">
              <i class="fa fa-plus" />
            </a>
          </li>
          <li class="nav-item dropdown active">
            <a class="nav-link dropdown-toggle"
               @click="openMyApp"
               href="#"
               data-toggle="dropdown">
              
            </a>
            <div class="dropdown-menu">
              <router-link :class="['dropdown-item', {'active': item.id == showId}]"
                           href="javascript:void(0);"
                           :to="{path: '/center', query: {showId: item.id}}"
                           v-for="item in $root.showList">
                {{item.title}}
                <span class="remove-warpper fr"
                      @click="removeShow(item.id, $event)">
                  <i class="fa fa-trash-alt" />
                </span>
              </router-link>
              <a href="#"
                 class="dropdown-item"
                 v-show="!$root.showList.length"></a>
            </div>
          </li>
          <!-- <li class="nav-item active">
                        <a class="nav-link" href="javascript:void(0);"></a>
                    </li> -->
          <li class="nav-item">
            <a id="logout"
               class="nav-link"
               href="javascript:void(0);"
               @click="logout"></a>
          </li>
        </ul>
        <ul class="navbar-nav ml-auto"
            v-else>
          <li class="nav-item active">
            <a class="nav-link"
               href="javascript:void(0);"
               @click="openModal('#loginModal')"></a>
          </li>
          <li class="nav-item">
            <a class="nav-link"
               href="javascript:void(0);"
               @click="openModal('#registerModal')"></a>
          </li>
        </ul>
      </div>
    </nav>
    <div id="loginModal"
         class="modal fade"
         tabindex="-1"
         role="dialog">
      <div class="modal-dialog modal-dialog-centered"
           role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h6 class="modal-title"
                id="exampleModalLabel"></h6>
            <button type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <form>
              <div class="input-hidden">
                <input type="password">
              </div>
              <div class="form-group">
                <label></label>
                <input class="form-control"
                       type="text"
                       v-model="info.loginname"
                       data-valid="['required']"
                       data-label=""
                       autocomplete="off">
              </div>
              <div class="form-group">
                <label></label>
                <input class="form-control"
                       type="password"
                       v-model="info.password"
                       data-valid="['required']"
                       data-label=""
                       autocomplete="off"
                       @keyup.13="login">
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <button type="button"
                    data-dismiss="modal"
                    data-toggle="modal"
                    data-target="#registerModal"
                    class="btn btn-sm btn-outline-info btn-cancel mr-auto">,</button>
            <button type="button"
                    class="btn btn-sm btn-info btn-ok"
                    @click="login"></button>
          </div>
        </div>
      </div>
    </div>
    <div id="registerModal"
         class="modal fade"
         tabindex="-1"
         role="dialog">
      <div class="modal-dialog modal-dialog-centered"
           role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h6 class="modal-title"
                id="exampleModalLabel"></h6>
            <button type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <form>
              <div class="form-group">
                <label></label>
                <input class="form-control"
                       type="text"
                       v-model="info.username"
                       data-valid="['required', 'base', 'checkname']"
                       data-label=""
                       autocomplete="off">
              </div>
              <div class="form-group">
                <label></label>
                <input class="form-control"
                       type="password"
                       v-model="info.password2"
                       data-valid="['required']"
                       data-label=""
                       autocomplete="off">
              </div>
              <div class="form-group">
                <label></label>
                <input class="form-control"
                       type="password"
                       v-model="info.password3"
                       data-valid="['required', 'passEQ(0)']"
                       data-label=""
                       autocomplete="off">
              </div>
              <div class="form-group">
                <label></label>
                <input class="form-control"
                       type="email"
                       v-model="info.email"
                       data-valid="['required', 'email', 'checkemail']"
                       data-label="">
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <button type="button"
                    data-dismiss="modal"
                    data-toggle="modal"
                    data-target="#loginModal"
                    class="btn btn-sm btn-outline-success btn-cancel mr-auto">,</button>
            <button type="button"
                    class="btn btn-sm btn-success btn-ok"
                    @click="register"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Md5, Util } from '@elegy7/spare'
const info = {
    loginname: 'admin',
    password: 'admin',
    password2: '', //
    password3: '', //
    email: ''
}
export default {
    data() {
        return {
            theme: 'light',
            loginModal: false,
            registerModal: false,
            info: Common.clone(info)
        }
    },
    computed: {
        showId() {
            return this.$route.query.showId
        }
    },
    methods: {
        openModal(elName) {
            $(elName).modal('show')
        },
        openMyApp() {
            $('.dropdown-toggle').dropdown()
        },
        login() {
            const self = this
            //
            const isPass = $('#loginModal')
                .one('hidden.bs.modal', function() {
                    $(this).clearValid()
                })
                .fireValid()
            if (!isPass) return
            Common.fetch({
                url: '/user/login',
                type: 'POST',
                data: {
                    loginame: this.info.loginname,
                    password: Md5(this.info.password),
                    showId: this.$route.query.showId
                },
                success(data, res) {
                    self.getMyShow()
                    self.getSystemShow()
                    Common.reloadAfterLogin && window.location.reload()
                    //session
                    Util.store.sessionSave('l', 'userinfo', data)
                    self.$root.userinfo = data
                    //,
                    $('#loginModal').modal('hide')
                    self.info = Common.clone(info)
                    Common.msg(res.summary)
                }
            })
        },
        register() {
            const self = this
            //
            const isPass = $('#registerModal')
                .one('hidden.bs.modal', function() {
                    $(this).clearValid()
                })
                .fireValid()
            if (!isPass) return
            Common.fetch({
                url: '/user/register',
                type: 'POST',
                native: true,
                data: {
                    username: this.info.username,
                    password: Md5(this.info.password2),
                    email: this.info.email
                },
                success(result) {
                    if (result.status) {
                        $('#registerModal').modal('hide')
                        $('#loginModal').modal('show')
                        self.info = Common.clone(info)
                        Common.msg(result.summary)
                    } else {
                        Common.msg(result.summary)
                    }
                }
            })
        },
        logout() {
            const self = this
            Common.fetch({
                url: '/user/logout',
                type: 'POST',
                success() {
                    self.$root.userinfo = {}
                    self.$root.unlogin()
                    $('.dropdown-toggle').dropdown('dispose')
                }
            })
        },
        createShow() {
            const self = this
            Common.fetch({
                url: '/show/createShow',
                success(showId) {
                    Common.msg('')
                    self.getMyShow()
                    // , 
                    if (self.$root.userinfo.type == 0) {
                        console.log('invoke')
                        self.getSystemShow()
                    }
                    if (self.showId) return
                    self.$router.push({ path: '/center', query: { showId } })
                }
            })
        },
        // ""
        getMyShow() {
            const self = this
            Common.fetch({
                url: '/show/getMyShow',
                type: 'GET',
                async: false,
                success(data) {
                    self.$root.showList = data
                }
            })
        },
        // ""
        getSystemShow() {
            const self = this
            Common.fetch({
                url: '/show/getSystemShow',
                type: 'GET',
                success(data) {
                    self.$root.sysShowList = data
                }
            })
        },
        /*  */
        removeShow(showId, e) {
            e.stopPropagation()
            const self = this
            if (showId == this.showId) {
                Common.msg('', '', 'warning')
                return
            }
            Common.Modal.confirm('?', '', function() {
                Common.fetch({
                    url: '/show/removeShow',
                    type: 'POST',
                    data: {
                        id: showId
                    },
                    success(data, res) {
                        if (res.status == 1) {
                            self.getMyShow()
                            // , 
                            if (self.$root.userinfo.type == 0) {
                                self.getSystemShow()
                            }
                            Common.msg('')
                            //,
                            if (data.redirect) window.location.href = '/index'
                        } else {
                            Common.msg('', '', 'danger')
                        }
                    }
                })
            })
        }
    },
    mounted() {
        $.extend(Common.Valid.asyncRules, {
            checkname: {
                validator(value) {
                    let exist = false
                    Common.fetch({
                        url: '/user/checkname',
                        type: 'GET',
                        data: {
                            username: value
                        },
                        async: false,
                        native: true,
                        success(result) {
                            if (result.status === 0) {
                                exist = true
                            }
                        }
                    })
                    return !exist
                },
                message: ''
            },
            checkemail: {
                validator(value) {
                    let exist = false
                    Common.fetch({
                        url: '/user/checkemail',
                        type: 'GET',
                        data: {
                            email: value
                        },
                        async: false,
                        native: true,
                        success(result) {
                            if (result.status === 0) {
                                exist = true
                            }
                        }
                    })
                    return !exist
                },
                message: ''
            }
        })
        $(this.$el).bindValid()
        /* ,  */
        if (this.$root.userinfo.username) {
            this.getMyShow()
            this.getSystemShow()
        }
    }
}
</script>
